.main-nav {
  display: grid;
  grid-template-columns: min-content 1fr;
  column-gap: 1.8vw;
  align-items: center;

  @media (max-width: 680px) {
    grid-template-columns: 1fr;
  }
}

.main-nav__logo {
  @media (max-width: 680px) {
    margin: 0 auto;
    margin-bottom: 42px;
  }
}











/*  Сворачивание Меню  */


.main-nav__toggle {
  position: absolute;
  display: none;
  top: 272px;
  left: 90px;
  width: 50px;
  height: 38px;
  background-color: transparent;
  padding: 0;
  border: none;
  outline: none;
  cursor: pointer;

  @media (max-width: 680px) {
    display: block;
    left: 40px;
  }
}

.main-nav--closed .main-nav__toggle::before {
  display: block;
  content: "";
  position: absolute;
  top: 0px;
  left: calc(50% - 20px);
  width: 40px;
  height: 3px;
  background-color: var(--basic-white);
  box-shadow: 0 8px 0 0 var(--basic-white), 0 16px 0 0 var(--basic-white);
}

.main-nav--closed .main-nav__toggle:hover::before,
.main-nav--closed .main-nav__toggle:focus::before {
  opacity: 0.5;
}

.main-nav--closed .main-nav__toggle:active::before {
  opacity: 0.2;
}

.main-nav--opened .main-nav__toggle {
  top: 242px;
  background-color: transparent;
}

.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 40px;
  height: 2px;
  background-color: var(--basic-white);
}

.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}

.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after,
.main-nav--opened .main-nav__toggle:focus::before,
.main-nav--opened .main-nav__toggle:focus::after {
  opacity: 0.5;
}

.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  opacity: 0.2;
}

.main-nav--closed .main-nav__list li {
  @media (max-width: 680px) {
    display: none;
  }
}

