.site-link {
  position: relative;
  color: var(--back-main);
  text-decoration: none;
}

.site-link--pink {
  border-bottom: 4px solid var(--basic-dark-pink);
}

.site-link--orange {
  border-bottom: 4px solid var(--basic-orange);
}

.site-link--green {
  border-bottom: 4px solid var(--basic-green);
}

.site-link--cool-pink {
  border-bottom: 4px solid var(--back-link);
}

.site-link--blue {
  border-bottom: 4px solid var(--basic-blue);
}
