.form-search {
  position: relative;
  display: flex;
  align-items: center;
  padding: 3px;
  border: 0.7px solid var(--basic-white);

  @media (max-width: 580px) {
    display: grid;
    grid-template-columns: repeat(2, max-content);
    justify-content: center;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 30px;
  }
}

.form-search input[type="search"] {
  width: 134px;
  height: 24px;
  font-size: 13px;
  color: var( --basic-white);
  margin-right: 5px;
  padding: 0 0 0 10px;
  background: transparent;
  border: 0;
  outline: 0;
  cursor: pointer;
  pointer-events: auto;

  @media (max-width: 1280px) {
    width: 100%;
  }
}

.form-search input[type="search"]::-webkit-input-placeholder {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.3);
}

.form-search:focus-within input[type="search"],
.form-search input[type="search"]:valid {

}

.form-search button {
  width: 24px;
  height: 24px;
  background: url("../img/search.svg") transparent;
  background-repeat: no-repeat;
  background-size: 100% auto;
  border: none;
  cursor: pointer;
  padding: 0;
}

.form-search input[type="search"]::-webkit-input-placeholder {
  visibility: hidden;
  font-family: inherit;
  color: rgba(255, 255, 255, 0.3);
}

.form-search:focus input[type="search"]::-webkit-input-placeholder,
.form-search:hover input[type="search"]::-webkit-input-placeholder {
  visibility: visible;
}

.form-search:focus input[type="search"],
.form-search:hover input[type="search"] {
  visibility: visible;
}
