.circle-block__item {
  position: absolute;
  width: clamp(264px, 20vw, 397px);
  z-index: 15;

  @media (max-width: 1600px) {

  }

  @media (max-width: 1280px) {

  }

  @media (max-width: 992px) {
    display: none;
  }
}

.circle-block__btn {
  position: relative;
  display: block;
  width: 48px;
  height: 48px;
  background: transparent, url("../img/marker-circle.png");
  background-repeat: no-repeat;
  background-size: 100% auto;
  border: none;
  cursor: pointer;
  z-index: 15;

  @media (max-width: 1600px) {
    background-size: 80% auto;
  }

  @media (max-width: 1280px) {
    background-size: 65% auto;
  }

  @media (max-width: 992px) {
    background-size: 55% auto;
  }
}

div.circle-block__card {
  position: absolute;
  top: -5vw;
  left: 2.5vw;
  z-index: -1;
}

div.circle-block__card--bottom {
  top: -2vw;
  left: 2.5vw;
}

div.circle-block__card--left {
  left: -346px;
}

.circle-block__card {
  position: relative;
  display: none;
  width: clamp(230px, 18vw, 346px);
  background-color: rgba(255, 255, 255, 0.8);
  text-align: center;
  padding: 20px;
  padding-bottom: 30px;

  @media (max-width: 1600px) {

  }

  @media (max-width: 1280px) {

  }
}

.circle-block__info.info-card {
  text-align: center;
  padding-top: 1vw;
  padding-left: 3.1vw;
  padding-right: 3.1vw;
  margin-top: 0;

  @media (max-width: 1600px) {
    padding: 0;
  }
}

.circle-block__info.info-card p {
  margin-top: 1.2vw;
}

.circle-block__info.info-card a {
  font-size: clamp(20px, 1.4vw, 25px);
  margin-top: 1.4vw;
}

.circle-block__info.info-card__link::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 4px;
  left: 50%;
  transform: translateX(-50%);
  bottom: -4px;
  background-color: var(--basic-green);
  text-decoration: none;
  border: none;
}

.circle-block__card img {

}

.circle-block__item:hover .circle-block__card,
.circle-block__item:focus .circle-block__card,
.circle-block__item:active .circle-block__card {
  display: block;
}
