.promotion img {
  position: relative;

  outline: 1px solid red;
}

.promotion > p {
  position: absolute;
  width: 100%;
  bottom: 75%;
  left: 50%;
  transform: translateX(-50%);
  font-family: inherit;
  font-size: clamp(20px, 1.5vw, 30px);
  font-variation-settings: "wght" 400, "wdth" 100;
  color: var(--back-main);
  text-align: center;
  margin: 0;
  z-index: 20;

  @media (max-width: 992px) {
    font-size: 28px;
  }

  @media (max-width: 480px) {
    font-size: 25px;
  }
}
