.crumbs {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
  padding-top: 32px;
  padding-bottom: 50px;
  padding-left: 6.8vw;
}

.crumbs li {
  font-size: clamp(16px, 1.25vw, 25px);
  color: var(--basic-green);
}

.crumbs li:last-child {
  margin-left: 10px;
}

.crumbs a {
  color: #8e8e8f;
  text-decoration: none;
  margin-left: 10px;
  margin-right: 10px;
}

.crumbs li:first-child a {
  margin-left: 0;
}


