.sidenav {
  display: grid;
  grid-template-rows: repeat(2,max-content);
  margin-bottom: 30px;

  @media (max-width: 767px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: 580px) {
    grid-template-columns: 1fr;
  }
}

.sidenav__list {
  padding-left: 6.8vw;
  margin-bottom: 8.5vw;
}

.sidenav__list a {
  display: block;
  font-size: clamp(20px, 1.4vw, 25px);
  font-variation-settings: "wght" 700, "wdth" 100;
  color: #2b2a29;
  text-decoration: none;
}

.sidenav__item:nth-child(1) > a {
  margin-top: 0;
}

.sidenav__item > a {
  margin-top: clamp(25px, 4vw, 49px);
  margin-bottom: 13px;
}

.sidenav__inner a {
  font-size: clamp(14px, 1.3vw, 20px);
  line-height: 18px;
  font-variation-settings: "wght" 400, "wdth" 100;
  margin-bottom: clamp(8px, 1vw, 15px);
}

a.sidenav__link-active {
  font-variation-settings: "wght" 700, "wdth" 100;
  color: var(--back-link);
  text-decoration: underline;
}

.sidenav .promotion {
  position: relative;
}

.sidenav .promotion img {
  display: grid;
  margin: 0 auto;
  outline: 1px solid blue;
}
