.site-list {
  display: grid;
  grid-auto-flow: column;
  column-gap: 40px;
  column-gap: 2vw;
  row-gap: 40px;
  margin-left: 5px;

  @media (max-width: 680px) {
    display: block;
    text-align: center;
  }
}

.site-list li {
  @media (max-width: 680px) {
    padding: 10px;
  }
}

.site-list a {
  position: relative;
  font-size: clamp(14px, 1.3vw, 20px);
  line-height: 28px;
  color: var(--basic-white);
  text-decoration: none;

  @media (max-width: 1280px) {
    font-size: 18px;
  }

  @media (max-width: 767px) {
    font-size: clamp(13px, 1.9vw, 15px);
  }

  @media (max-width: 680px) {
    font-size: 20px;
  }

  @media (max-width: 480px) {
    font-size: 25px;
  }

  &:hover {
    color: var(--back-link);
  }
}

.site-list__item--active a {
  color: var(--back-link);
  border-bottom: 4px solid var(--back-link);
  padding-bottom: 5px;
}
