.logo-mebel {
  width: 77px;
  height: 77px;


  @media (max-width: 680px) {
    width: 158px;
    height: 158px;
    margin-bottom: 42px;
  }
}

.logo-mebel__img--mobile {
  display: none;

  @media (max-width: 680px) {
    display: block;
  }
}

.logo-mebel__img--desktop {
  display: block;

  @media (max-width: 680px) {
    display: none;
  }
}
